<template>
  <div class="detail">
    <Navbar />
    <div class="row">
      <div class="col-sm-3">
        <SideNavBar class="mobile-none" />
      </div>
      <div class="col-sm-9">
        <Products />
      </div>
    </div>
    <div class="footer mt-5">
      <FootNavBar class="mobile-show" />
      <Footer />
    </div>
  </div>
</template>

<script>
import Footer from "../components/Footer.vue";
import Navbar from "../components/Navbar.vue";
import SideNavBar from "../components/SideNavbar.vue";
import Products from "../components/Products.vue";
import FootNavBar from "../components/FootNavBar.vue";
import ProductAPI from "./../apis/products";
import { Toast } from "./../utils/helpers";

export default {
  components: { Navbar, SideNavBar, Footer, Products, FootNavBar },
  name: "ProductDetail",
  data() {
    return {
      SingleProduct: {
        id: -1,
        title: "",
        price: "",
        category: "",
        description: "",
        image: "",
      },
    };
  },
  created() {
    const { id: productId } = this.$route.params;
    this.fetchSingleProduct(productId);
  },
  methods: {
    async fetchSingleProduct(productId) {
      try {
        const { data } = await ProductAPI.getSingleProduct({ productId });
        console.log("data", data);
        this.SingleProduct = data;
      } catch (error) {
        console.log(error);
        Toast.fire({
          icon: "error",
          title: "無法取得商品資料，請稍後再試",
        });
      }
    },
  },
};
</script>
