<template>
  <div class="row">
    <Spinner v-if="isLoading" />
    <template v-else>
      <div class="col-sm-6">
        <img :src="SingleProduct.image" class="p-5" alt="" />
      </div>
      <div class="col-sm-6 p-5">
        <h4>{{ SingleProduct.title }}</h4>
        <div v-if="!isFavorite" class="icon-list">
          <font-awesome-icon
            :icon="['far', 'heart']"
            size="lg"
            class="icon heart mt-4 me-2"
            @click.prevent="
              addFavoriteItem(
                SingleProduct.title,
                SingleProduct.image,
                SingleProduct.price,
                SingleProduct.id
              )
            "
          />
          <p class="mt-4 wish">WISH LIST</p>
        </div>
        <div v-else class="icon-list">
          <font-awesome-icon
            :icon="['fas', 'heart']"
            size="lg"
            class="icon heart mt-4 me-2"
            @click.prevent="removeFromFavorite(index)"
          />
          <p class="mt-4 wish">WISH LIST</p>
        </div>
        <p class="mt-4 bold">US$ {{ SingleProduct.price }}</p>
        <div class="count mt-2">
          <font-awesome-icon
            :icon="['fas', 'minus-circle']"
            size="1x"
            class="me-4 icon"
            @click.prevent="handleMinus()"
          />
          {{ count }}
          <font-awesome-icon
            :icon="['fas', 'plus-circle']"
            size="1x"
            class="ms-4 me-3 icon"
            @click.prevent="handlePlus()"
          />
        </div>
        <div class="description mt-4">
          <p>
            {{ SingleProduct.description }}
          </p>
        </div>
        <button
          class="mt-4 btn"
          @click.prevent="
            addShoppingCart(
              SingleProduct.title,
              SingleProduct.image,
              SingleProduct.price,
              count,
              SingleProduct.id
            )
          "
        >
          加入購物車
        </button>
      </div>
    </template>
  </div>
</template>
<script>
import ProductAPI from "./../apis/products";
import { Toast } from "./../utils/helpers";
import Spinner from "./Spinner.vue";

export default {
  components: { Spinner },
  name: "Products",
  data() {
    return {
      SingleProduct: {
        id: -1,
        title: "",
        price: "",
        category: "",
        description: "",
        image: "",
      },
      count: 1,
      isFavorite: false,
      isLoading: true,
    };
  },
  created() {
    const { id: productId } = this.$route.params;
    this.fetchSingleProduct(productId);
  },
  methods: {
    async fetchSingleProduct(productId) {
      try {
        const { data } = await ProductAPI.getSingleProduct({ productId });
        this.SingleProduct = data;
        this.isLoading = false;
      } catch (error) {
        this.isLoading = false;
        console.log(error);
        Toast.fire({
          icon: "error",
          title: "無法取得商品資料，請稍後再試",
        });
      }
    },
    handlePlus() {
      this.count++;
    },
    handleMinus() {
      if (this.count > 1) {
        this.count--;
      }
    },
    addShoppingCart: function(title, image, price, count, id) {
      this.$store.commit("setShoppingCart", {
        title: title,
        image: image,
        price: price,
        count: count,
        id: id,
      });
      Toast.fire({
        icon: "success",
        title: "已加入購物車",
      });
    },
    addFavoriteItem: function(title, image, price, id) {
      this.isFavorite = true;
      this.$store.commit("setFavoriteItem", {
        title: title,
        image: image,
        price: price,
        id: id,
      });
      Toast.fire({
        icon: "success",
        title: "已加入收藏清單",
      });
    },
    removeFromFavorite: function(index) {
      this.isFavorite = false;
      this.$store.commit("deleteFavorite", index);
      Toast.fire({
        icon: "success",
        title: "已移除收藏",
      });
    },
  },
};
</script>

<style scoped>
h4,
p {
  text-decoration: none;
}
img {
  width: 100%;
  height: 100%;
}
.btn {
  border: 1px solid rgb(179, 174, 174);
  background-color: #fff;
  font-size: 13px;
  width: 100px;
  height: 40px;
}
.icon {
  cursor: pointer;
}
.icon-list {
  display: flex;
  flex-direction: row;
}
.heart {
  color: rgb(213, 171, 171);
}
.wish {
  font-size: 13px;
  color: rgb(148, 119, 119);
}
</style>
